      .bloque-texto-g__texto.p-5
        p.mb-0 El dibujo es una forma expresin humana y una de las ms efectivas, ya que bien ejecutado es capaz de superar incluso la barrera del idioma, tanto as que algunos se han constituido en smbolos que son mundialmente utilizados para representar conceptos especficos; por ejemplo, la seal de prohibido o las figuras de hombre y mujer que se ponen en la entrada de los baos pblicos.<br><br>En su definicin ms bsica, el dibujo es el arte de representar objetos mediante el uso de lneas sobre una superficie, y es una de las formas ms sencillas de representacin grfica.
      
    p.mb-5 Actualmente, las tcnicas de dibujo se dividen en dos grandes ramas definidas principalmente por la naturaleza de las herramientas usadas y estas son las tcnicas anlogas y las digitales. Las tcnicas anlogas hacen referencia a todas aquellas que hacen uso de instrumentos o herramientas tradicionales como lo son el lpiz y el papel principalmente, aunque no se restringen solo a estos 2, mientras que las digitales hacen uso de computadores y una amplia gama de herramientas tanto de <i>hardware</i> como de <i>software</i>.
    
    .row.justify-content-center.t1-fondo1.pb-5
      .col-10.pt-5.px-4
        figure.mb-5
          img(src='@/assets/curso/tema4/img02.png') 
        p.mb-0 A su vez dentro de estas 2 ramas existen un gran abanico de categoras, las cuales se definen principalmente por el tipo de materiales que se emplean para realizarlas; por ejemplo, tcnicas secas o aguadas dentro de las anlogas o tcnicas vectoriales o mapas de <i>bits</i> para el caso de las digitales. Sin embargo, al ser la intencin de este documento hacer una introduccin bsica se abordarn una serie de conceptos tiles para realizar una representacin grfica de ideas simple y efectiva orientada a la realizacin de <i>storyboard</i>.
    
