    #t_4_1.titulo-segundo.color-acento-contenido
      h2 4.1 Herramientas de dibujo

    p.mb-5 Para dibujar existen muchos instrumentos, utensilios y herramientas, a continuacin, se da una breve descripcin de algunos de los ms utilizados clasificados segn su uso:

    .row.justify-content-center.t4-fondo1.pb-5
      .col-10.pt-5.px-4
        figure.mb-5
          img(src='@/assets/curso/tema4/img03.png') 

    div.LineaTiempoD-t4.mb-5
      LineaTiempoD.color-secundario
        div(numero="A" titulo="Lpices y portamina")    
          p.mb-4 El lpiz es un instrumento para dibujo o escritura que consiste habitualmente en una barra (conocida tambin como mina) compuesta de una mezcla de grafito y arcilla o cera encapsulada en un cilindro de madera (actualmente se usan muchos otros materiales como plstico, papel, etc.) que le da soporte a la mina, tambin son fabricados de colores mediante el uso de diferentes pigmentos, pero para dibujo el ms usado suele ser el lpiz de grafito.
          p.mb-5 Los lpices de grafito se marcan con letras y nmeros segn la composicin de la mina usando la H (del ingls Hard) para las minas duras y la B (del ingls Back) para las minas blandas.
          figure.mb-5
            img(src='@/assets/curso/tema4/img04.svg') 
          p.mb-0 El portaminas es otro instrumento de dibujo anlogo al lpiz con la diferencia de que tiene un sistema mecnico para proveer las minas, las cuales se pueden reemplazar una vez se acaban.
        
        div(numero="B" titulo="Bolgrafos y plumas")
          p.mb-5 Son instrumentos de escritura que cuentan con sistemas mecnicos para dosificar tinta. Aunque principalmente son usados para escribir es comn que algunos artistas los usen para dibujar.
          figure
            img(src='@/assets/curso/tema4/img05.png') 

        div(numero="C" titulo="Sacapuntas y bistur")  
          p.mb-5 Los sacapuntas son utensilios usados para desbastar el recubrimiento de madera de los lpices a medida que se va desgastando la mina, existen muchos modelos de sacapuntas, pero su finalidad es la misma, tambin es comn hacer uso de cuchillas como el bistur para hacer el desbaste de la madera y afilar las puntas de las minas a medida que se gastan.
          figure
            img(src='@/assets/curso/tema4/img06.png') 

        div(numero="D" titulo="Papeles")  
          p.mb-4 El papel es un material obtenido del procesamiento de fibras vegetales de las que se obtiene pulpa de celulosa mediante mtodos de molienda, la cual es procesada y prensada para obtener lminas delgadas de caractersticas variables segn el proceso que se les aplique.
          p.mb-5 Existen diferentes tipos de papel destinados a variedad de aplicaciones, aunque en principio todos sirven para dibujar, entre los ms comunes y fciles de conseguir se encuentra el papel bond de 75 gramos que regularmente es de uso de oficina, sin embargo, en casi cualquier papelera o tienda de implementos de arte se consiguen ms variedades.
          figure
            img(src='@/assets/curso/tema4/img07.png') 

        div(numero="E" titulo="Gomas de borrar")          
          p.mb-5 Son instrumentos diseados para eliminar trazos de lpiz y de algunas tintas borrables, hay de consistencia rgida, aunque con algo de flexibilidad y algunas maleables como la goma limpia tipos.
          figure
            img(src='@/assets/curso/tema4/img08.png') 

    .row.justify-content-center.t4-fondo1.pb-5
      .col-10.pt-5.px-4
        figure.mb-5
          img(src='@/assets/curso/tema4/img09.png') 

    h2 <i>Hardware</i>
    p.mb-5 Actualmente existen muchas alternativas de equipos de cmputo y perifricos tiles para desarrollar ilustraciones que van desde los telfonos mviles, hasta computadores de escritorio pasando por computadores y porttiles, <i>tablets</i> y otros perifricos especializados en la ilustracin digital 2D y 3D.
    
    div.LineaTiempoD-t4.mb-5
      LineaTiempoD.color-secundario
        div(numero="A" titulo="Telfonos mviles")    
          p.mb-5 Actualmente la capacidad de cmputo de los telfonos mviles supera con creces la poseda por los computadores de escritorio de finales de los 90, especialmente los que encajan en la categora de Smartphone, ya que en su mayora poseen una pantalla tctil de suficiente tamao que brinda la posibilidad de dibujar sobre ella como si se tratara de una libreta de bolsillo digital, gracias a esta caracterstica y las diferentes aplicaciones y algunos utensilios como los <i>stylus</i> (este ltimo no es indispensable) los <i>smartphones</i> se convierten en una alternativa al alcance de la mayora de personas para realizar dibujos digitales.
          figure
            img(src='@/assets/curso/tema4/img10.png') 

        div(numero="B" titulo="Tablets")    
          p.mb-4 Son dispositivos con grandes pantallas tctiles, habitualmente mayores a 7 y que funcionan generalmente bajo los mismos sistemas operativos que los <i>Smartphones</i>, como lo son android, iOs y en algunos casos <i>Windows</i>, gracias a esto la mayora de aplicaciones disponibles para telfonos mviles estn disponibles tambin para las <i>tablets</i> y dado su tamao de pantalla son una alternativa ms cmoda para dibujar que un <i>smartphone</i>.
          p.mb-5 El dispositivo ms usado de esta categora por artistas dedicados a la ilustracin sin lugar a dudas es el iPad de Apple, sin embargo, en el mercado existen muchas otras alternativas con funcionalidades iguales o similares en un amplio rango de precios.
          figure
            img(src='@/assets/curso/tema4/img11.png') 
                      
        div(numero="C" titulo="Computadores")    
          p.mb-4 Los computadores son la alternativa ms utilizada en cuanto a trabajo profesional se refiere, principalmente por las posibilidades de personalizacin segn las necesidades y porque ofrecen ms capacidad de cmputo que alternativas como mviles o <i>tablets</i> y por tanto son capaces de ejecutar aplicaciones ms potentes para ilustracin.
          p.mb-5 Existen en 2 grandes categoras que son equipos de escritorio y equipos porttiles diferencindose principalmente por su volumen y peso lo que hace que el equipo deba ser usado en una estacin fija para el caso de los equipos de escritorio o tenga la posibilidad de ser transportado con libertad para el caso de los porttiles (conocidos tambin como <i>laptops</i>).
          figure
            img(src='@/assets/curso/tema4/img12.png') 

        div(numero="D" titulo="Tabletas grficas")    
          p.mb-4 Las tabletas grficas son un perifrico de entrada que se conecta a un computador de escritorio o porttil, e incluso algunos modelos actuales permiten conexin con <i>smartphones</i> compatibles, constan de una superficie plana sobre la cual se interacta por medio de un lpiz que es capaz de detectar la posicin, contacto, presin y en ocasiones inclinacin para transferir esa informacin al trazo representado en pantalla.
          p.mb-5 Las hay de muchas marcas, modelos y precios siendo los fabricantes especializados ms conocidos Wacom, Xp Pen y Huion, existen otros fabricantes, pero por presencia en el mercado los modelos de estos tres fabricantes son los ms sencillos de encontrar en la regin de Latinoamrica.
          figure
            img(src='@/assets/curso/tema4/img13.png') 
                      
        div(numero="E" titulo="Pantallas grficas")    
          p.mb-4 En esencia tienen el mismo funcionamiento que una tableta grfica, salvo que la superficie sobre la cual se interacta con el lpiz funciona tambin como monitor, razn por la cual la experiencia de uso es ms natural, pero en contraposicin su costo es ms elevado.
          p.mb-5 Al igual que con las tabletas grficas, las marcas de dispositivos ms fciles de encontrar en la regin son Wacom, Xp Pen y Huion.
          figure
            img(src='@/assets/curso/tema4/img14.png') 
                      
    h2 <i>Software</i>
    p.mb-4 El <i>software</i> es el conjunto de instrucciones (Programas) que permiten la utilizacin de los equipos de cmputo, el principal es el sistema operativo el cual cumple la funcin de ser base para la operacin del equipo y de otros programas. Las plataformas ms populares actualmente para ordenadores de escritorio y porttiles son <i>Windows</i>, macOS y Linux en sus diversas distribuciones y iOS y Android para dispositivos mviles, lo que ocasiona que la mayora de aplicaciones para dibujo se centren en estos sistemas operativos.
