        p.mb-4 El trmino boceto es utilizado para definir una exploracin hecha previa a la realizacin de una obra, en l se utilizan trazos simples para tratar de representar de manera sencilla y rpida lo que se quiere comunicar.
        p.mb-0 Adems de ser una aproximacin inicial tambin, suele hacerse como mtodo de estudio para lograr la comprensin sobre las formas que se quieren representar ms adelante o simplemente para adquirir y mejorar habilidades.

