          p.mb-0 En conclusin, la bocetacin es tanto un mtodo de estudio como una solucin rpida para la representacin grfica de ideas convirtindolo en algo eficaz para la planeacin de una composicin pictrica en el arte, el planteamiento inicial de algo en el diseo o el desarrollo de <i>storyboard</i> en el audiovisual entre muchos otros usos.<br><br>Aunque el boceto se caracteriza por ser una construccin rpida es importante tener presentes algunos conceptos que se describirn a continuacin, ya que son elementos bsicos del dibujo y son importantes para hacer del proceso de bocetacin algo eficiente.
        
