    p.mb-5 Es la unidad mnima de la comunicacin grfica y por tanto de donde se origina todo dibuj, segn el uso que se le d puede generar distintas sensaciones como por ejemplo al agruparlos uno detrs de otro de manera continua dan origen a la lnea o al dispersarse sobre una superficie pueden dar la sensacin de un plano. Sus cualidades grficas dependen del soporte donde es plasmado, ya sea papel, tela, cartn, etc. y el instrumento usado para marcarlo al apoyarlo sobre la superficie, lpiz, bolgrafo, marcador, prima, etc.
    
    p.text-center.mb-5 <b>Estas cualidades que lo definen son:</b>
    
