          p.mb-4 La perspectiva es una tcnica de representacin que busca mostrar fielmente sobre una superficie de dos dimensiones los objetos tal cual como se ven en el mundo real al crear la sensacin de profundidad.
          p.mb-4 El dibujo no tiene profundidad estrictamente hablando, es decir, cuando se representa un objeto en un plano este solo existe en 2 dimensiones que son ancho y alto o segn otras definiciones, horizontal y vertical o X y Y.
          p.mb-4 En el dibujo existen muchos tipos de perspectiva, pero a continuacin el foco se dar en las ms usadas en el entorno artstico que se valen de la lnea de horizonte la cual es una analoga de la lnea recta en la lejana que separa el mar o la tierra del cielo, por otro lado hace uso de puntos de fuga, que son puntos donde convergen las lneas proyectadas hacia el horizonte para crear la sensacin de profundidad, los cuales se ubican sobre la lnea de horizonte para el caso de las perspectivas de 1 y 2 puntos o en algn lugar sobre o bajo la lnea de horizonte para el caso de la perspectiva de 3 puntos de fuga.
    
