        p.mb-4 Una manera de hacer bocetos rpidos de un personaje, ya sea como exploracin al momento de disear o para ver distintas actitudes de un diseo ya establecido es el uso de figuras geomtricas bsicas y lneas de accin.
        p.mb-4 La abstraccin consiste en la simplificacin de volmenes complejos de forma que con pocos trazos y el uso de figuras bsicas como cuadrados, crculos, rectngulos, tringulos, valos y dems se logre capturar la esencia elemental del personaje.
        p En este orden de ideas si un personaje es delgado van a prevalecer en su construccin formas como los rectngulos, en el caso de un personaje obeso las figuras que ms resalta seguramente sern crculos y valos, para un personaje de gran musculatura pueden aparecer muchos valos mezclados con cuadrados y trapecios y as indefinidamente dependiendo de la fisonoma que se desee representar.
    
