        ) 
        .bloque-texto-g__texto.p-5
          p.mb-0 Junto al uso de figura las lneas de accin ayudan a determinar el dinamismo del personaje puesto que determinan tanto el flujo del movimiento para poses dinmicas como la postura para poses estticas, generalmente se usan para definir el contorno de la espina dorsal y desde ah generar la posicin de las extremidades y el resto del personaje.<br><br>Para trazar lneas de accin lo ms comn es usar formas de C y S en su direccin natural como invertidas variando la apertura de las curvas segn la pose o movimiento que se desee representar e incluso en ocasiones lneas rectas pero es algo poco comn ya que aunque pueden marcar una direccin no suelen denotar ningn tipo de dinmica en el movimiento y por tanto generan poses aburridas.

