        p.mb-4 En trminos generales las tramas son lneas y/o puntos usados para generar diferentes sensaciones como volumen, textura, luz y sombra dependiendo de cmo se apliquen, por ejemplo, si se trazan lneas o puntos muy juntos entre s se puede crear la sensacin de oscuridad y as mismo dar paso a la claridad a medida que se va dejando ms espacio entre cada elemento grfico trazado.
        p.mb-0 Su variedad es tan amplia como pueda imaginarse, partiendo de lneas paralelas, lneas cruzadas, lneas rectas o curvas, lneas mixtas, puntos, figuras, etc. Del mismo modo es posible generar combinaciones entre distintos tipos de lnea, longitud de los grafismos orientacin y valor obteniendo as posibilidades de combinacin prcticamente infinitas.
      
