        p.mb-0 Su forma de uso depende principalmente de la intencin del artista, sin embargo, es recomendable hacer uso del tramado mediante la aplicacin de capas para de esta manera ir acentuando las partes ms oscuras y al mismo tiempo obtener ms riqueza en la textura, en la siguiente imagen podemos apreciar un ejemplo de cmo se va ganando en intensidad y textura a medida que se van sumando capas.<br><br>Uno de los mejores ejemplos de aplicacin de tramas se encuentra en los billetes donde podemos ver diferentes tipos de tramas que van desde puntos en diferentes tamaos y densidades, pasando por lneas con diferentes valores, direcciones, separaciones y tipos hasta patrones de figuras geomtricas.

      
